<template>
<html lang="en">
<head>
    <meta charset="UTF-8">
    <meta name="viewport"
          content="width=device-width, user-scalable=no, initial-scale=1.0, maximum-scale=1.0, minimum-scale=1.0">
    <meta http-equiv="X-UA-Compatible" content="ie=edge">
    <title></title>
</head>
<body>
    <div id="cart">
    <section class="cartMain">
        <div class="cartMain_hd">
            <ul class="order_lists cartTop">
                <li class="list_chk">
                    <!---->
                    <input type="checkbox" id="all" class="whole_check">
                    <label for="all" :class="fetchData.status?'mark':''" @click="cartchoose()"></label>
                    
                </li>
                <li class="list_con"></li>
                <li class="list_info"></li>
                <li class="list_price"></li>
                <li class="list_amount"></li>
                <li class="list_sum"></li>
                <li class="list_op"></li>
               
            </ul>
        </div>

         <div class="cartBox" v-for="item in fetchData.list" key="index">
            <div class="shop_info">
                <div class="all_check">
                    <input type="checkbox" id="shop_a" class="shopChoice">
                    <label for="shop_a" class="shop" :class="item.check?'mark':''" @click="shopchoose(item)"></label>
                </div>
                <div class="shop_name">
                    <a href="javascript:;">{{item.shop_name}}</a>
                </div>
            </div>
            <div class="order_content">
                <ul class="order_lists" v-for="pro in item.products">
                    <li class="list_chk">
                        <input type="checkbox" id="checkbox_2" class="son_check">
                        <label for="checkbox_2" :class="pro.checked?'mark':''" @click="choose(item,pro)"></label>
                    </li>
                    <li class="list_con">
                        <div class="list_img"><a href="javascript:;"><img :src="pro.img" alt=""></a></div>
                        <div class="list_text"><a href="javascript:;">{{pro.text}}</a>
                        <!-- <span class="list_custom"></span> -->
                        </div>
                    </li>
                    <li class="list_info">
                        <p></p>
                        <p>16*16*3(cm)</p>
                    </li>
                    <li class="list_price">
                        <p class="price">{{pro.price}}</p>
                        <div class="charge">
                            <!-- <p></p> -->
                            <div class="chargebox">
                                
                            </div>
                        </div>
                    </li>
                    <li class="list_amount">
                        <div class="amount_box">
                            <a href="javascript:;" class="reduce reSty" @click="reduce(pro)">-</a>
                            <input type="text" v-model="pro.num" class="sum" v-on:input="calculate(pro)">
                            <a href="javascript:;" class="plus" @click="add(pro)">+</a>
                        </div>
                    </li>
                    <li class="list_sum">
                        <p class="sum_price">{{pro.sum}}</p>
                    </li>
                    <li class="list_op">
                        <p class="collect"><a href="javascript:;" class="colBtn"></a></p>
                        <p class="del"><a href="javascript:;" class="delBtn"></a></p>
                        <!-- <p class="custom"><a href="javascript:;" class="cusBtn"></a></p> -->
                        <div class="custombox">
                                
                        </div>
                    </li>
                </ul>
            </div>
        </div> 

        
        
        <!---->
        <div class="bar-wrapper">
            <div class="bar-right">
                <div class="piece"><strong class="piece_num">{{this.fetchData.allnum}}</strong></div>
                <div class="totalMoney">: <strong class="total_text">{{this.fetchData.allsum}}</strong></div>
                <div class="calBtn"><a href="javascript:;"></a></div>
            </div>
        </div>
    </section>
    <section class="model_bg"></section>
    <section class="my_model">
        <p class="title"><span class="closeModel">X</span></p>
        <p></p>
        <div class="opBtn"><a href="javascript:;" class="dialog-sure"></a><a href="javascript:;" class="dialog-close"></a></div>
    </section>
    </div>
</body>
</html>
</template>

<script>
export default ({
  name: 'cart',
  data(){
    return{
      fetchData:{
        list:[
          {
            shop_id:1,
            shop_name:'',
            products:[
          {
            pro_id:101,
            text:'ICS',
            price:480,
            num:1,
            img:'../../assets/images/logo.png',
            sum:480,
            checked:false // 
          },
          {
            pro_id:102,
            text:'WEB',
            price:680,
            num:1,
            img:'../../assets/images/logo.png',
            sum:680,
            checked:false
          },
          {
            pro_id:103,
            text:'SE',
            price:380,
            num:1,
            img:'../../assets/images/logo.png',
            sum:380,
            checked:false
          }
        ],
        check:false, // 
        choose:0, // 
      },
      {
        shop_id:2,
        shop_name:'',
        products:[
          {
            pro_id:201,
            text:'CSE',
            price:580,
            num:1,
            img:'../../assets/images/logo.png',
            sum:580,
            checked:false
          },
          {
            pro_id:202,
            text:'GRE',
            price:780,
            num:1,
            img:'../../assets/images/logo.png',
            sum:780,
            checked:false
          }
        ],
        check:false,
        choose:0,
      },
      {
        shop_id:3,
        shop_name:'',
        products:[
          {
            pro_id:301,
            text:'GMAT',
            price:180,
            num:1,
            img:'../../assets/images/logo.png',
            sum:180,
            checked:false
          },
          {
            pro_id:302,
            text:'TOFEL',
            price:280,
            num:1,
            img:'../../assets/images/logo.png',
            sum:280,
            checked:false
          }           
        ],
        check:false,
        choose:0,
      }
    ],
        status:false, // 
        allchoose:0, // 
        allsum:0, // 
        allnum:0, // 
      }
    }
  },
  methods:{
    choosetrue(item,pro){
      pro.checked=true // true
      ++item.choose===item.products.length?item.check=true:'' // +1true
      item.check?++this.fetchData.allchoose===this.fetchData.list.length?this.fetchData.status=true:this.fetchData.status=false:''//true+1true     
      this.fetchData.allsum+=pro.sum
      this.fetchData.allnum+=parseInt(pro.num)
    },
    choosefalse(item,pro){
      pro.checked=false // false
      --item.choose // -1
      if(item.check){ // 
        item.check=false
        --this.fetchData.allchoose // -1
      }
      this.fetchData.status=false // false
      this.fetchData.allsum-=pro.sum // 
      this.fetchData.allnum-=pro.num
    },
    choose(item,pro){
      !pro.checked?this.choosetrue(item,pro):this.choosefalse(item,pro)
    },
    shoptrue(item){
      item.products.forEach((pro)=>{
        pro.checked===false?this.choosetrue(item,pro):'' // choosetrue
      })
    },
    shopfalse(item){
      item.products.forEach((pro)=>{
        pro.checked===true?this.choosefalse(item,pro):'' // choosefalse
      })
    },
    shopchoose(item){
      !item.check?this.shoptrue(item):this.shopfalse(item)
    },
    cartchoose(){
      this.fetchData.status=!this.fetchData.status // 
      this.fetchData.status?this.fetchData.list.forEach((item)=>this.shoptrue(item)):this.fetchData.list.forEach((item)=>this.shopfalse(item))//   
    },
    add(pro){
      pro.num++
      pro.checked?this.fetchData.allnum++:''
      pro.sum+=pro.price
      pro.checked?this.fetchData.allsum+=pro.price:this.fetchData.allsum
    },
    reduce(pro){
      if(pro.num===1){
        pro.num
      }else{
        pro.num--
        pro.checked?this.fetchData.allnum--:''
        pro.sum-=pro.price
        pro.checked?this.fetchData.allsum-=pro.price:this.fetchData.allsum
      }
    },
    calculate (pro) {
      let oldsum = pro.sum // 
      let oldnum = oldsum / pro.price // 
      pro.num = parseInt(pro.num) // 
      pro.num > 0 ? pro.sum = pro.num * pro.price:pro.num=oldnum  //  0
      let diffsum = pro.sum-oldsum  // 
      let diffnum = pro.num-oldnum  // 
      if (pro.checked) { 
        //     
        this.fetchData.allsum += diffsum // 
        this.fetchData.allnum += diffnum // 
      }
    }
  }
})
</script>

<style>

</style>



